/* App.css */
body {
  margin: 0;
  font-family: "Segoe UI", sans-serif;
  background-color: #dff3e3;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.card {
  background: #ffffffcc;
  border-radius: 16px;
  padding: 30px 40px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  width: 90%;
  text-align: center;
}

h1 {
  font-size: 28px;
  color: #335;
  margin-bottom: 24px;
}

input {
  padding: 12px 16px;
  font-size: 16px;
  border-radius: 10px;
  border: 1px solid #bbb;
  width: 100%;
  box-sizing: border-box;
}

.metric {
  margin-bottom: 20px;
}

.metric-label {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  margin-bottom: 6px;
  color: #333;
}

.metric-bar {
  background-color: #cdd;
  border-radius: 12px;
  height: 16px;
  position: relative;
  overflow: hidden;
}

.fill {
  height: 100%;
  border-radius: 12px 0 0 12px;
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 20px;
}
